import React from "react";
import Link from "gatsby-link";

export default class VideoButtons extends React.PureComponent {

    constructor() {
        super();
        this.state = {
            video_urls: {
                demo_reel: ["https://www.youtube.com/embed/zlcn0HA8u3A", "../static/images/demo_reel_cv.jpg", "Demo Reel"],
                demo_reel_360: ["https://www.youtube.com/embed/PIlKxRVYpmQ", "../static/images/360_demo_reel_cv.jpg", "360 Demo Reel"],
                magazin: ["https://www.youtube.com/embed/cR-tsqBJlBs", "../static/images/magazin_cv.jpg", "Tv Intro"],
                tv_intro: ["https://www.youtube.com/embed/_KQ8aFCzSDI", "../static/images/lrt_cv.jpg", "Logo Sting"],
                hedgehog: ["https://www.youtube.com/embed/Fq8NZCC6pQ8", "../static/images/hedgehog_cv.jpg", "Logo Sting"],
                conference: ["https://www.youtube.com/embed/PMj8xLXzz9M", "../static/images/conference_cv.jpg", "Promo"]
            }
        }
    }

    handleButtonsClick(url) {
        this.props.openLayer(url);
    }

    render() {

        const buttons = Object.entries(this.state.video_urls).map((arr, index) => {
            return (
                <div className="button-wrapper" key={index}>
                    <button onClick={this.handleButtonsClick.bind(this, arr[1][0])}>
                        <img src={arr[1][1]} alt=""/>
                        <div className="hover"></div>
                    </button>
                    <h3 className="button-title">{arr[1][2]}</h3>
                </div>
            );
        });
        return <div className="video-buttons">
        {buttons}
        </div>;
    }
}



