import React from "react";
import Animate from "grommet/components/Animate";
import Box from "grommet/components/Box";
import Layer from 'grommet/components/Layer';
import VideoButtons from "../components/Content/VideoButtons";

export default class VideoProduction extends React.Component {
  constructor() {
    super();
    this.state = {
      color: undefined,
      layer:{
        start:false,
        url: ""
      }
    }
    this.getLayer = this.getLayer.bind(this);
    this.handleOnClose = this.handleOnClose.bind(this);
    this.handleOpenLayer = this.handleOpenLayer.bind(this);
  }
  componentDidMount() {
    let colors = this.props.setColorOfContent();
    this.setState({
      color: colors.video
    });
  }

  getLayer(){
    if(this.state.layer.start){
      return <Layer closer={true} flush={true} align={"center"} overlayClose={true} onClose={this.handleOnClose}>
      <div className="iframe-wrapper">
          <iframe width="560" height="349" src={this.state.layer.url} frameBorder="0" ></iframe>
      </div>
    </Layer>
    } else{
      return "";
    }
  }

  handleOnClose(){
    this.setState({
      layer:{
        start:false,
        url: ""
      }
    });
  }

  handleOpenLayer(url){
    this.setState({
      layer:{
        start:true,
        url:url
      }
    });
  }

  render() {
    let style = {
      backgroundColor: this.state.color + "1a"
    }
    let layer = this.getLayer();
    return (
        <Animate
          enter={{ animation: "slide-right", duration: 300, delay: 0 }}
          keep={true}
        >
        <Box className="content-box video" style={style}>
          <VideoButtons openLayer={this.handleOpenLayer} />
        {layer}
          </Box>
        </Animate>
    );
  }
}



